@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.oke {
  font-size: 10px;
}
.navbar-nav-left {
  text-decoration: none;
}

:root {
  --color-primary: #006241;
  --color-secondary: #d50032;
  --color-extra: #d4e9e2;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html,
body {
  font-family: 'Open Sans', sans-serif;
  line-height: 1.4;
  color: #000;
}

a {
  color: #000;
}

ul {
  list-style: none;
}

p {
  margin: 5px 0;
  line-height: 1.7;
}

/* start Navbar */

.navbar {
  position: fixed;
  width: 100%;
  height: auto;
  background-color: #fff;
  padding: 20px;
  box-shadow: 0 1px 3px rgb(0 0 0 / 10%), 0 2px 2px rgb(0 0 0 / 6%), 0 0 2px rgb(0 0 0 / 7%);
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* max-width: 1440px; */
  /* margin: 0 auto; */
}

.navbar ul {
  display: flex;
  align-items: center;
}

.navbar li {
  margin: 0 15px;
  font-weight: bold;
}

.navbar a {
  color: #000;
  text-decoration: none;
}

.navbar a:hover {
  color: #006241;
  color: var(--color-primary);
}

.navbar-brand img {
  width: 50px;
  height: 50px;
}

.navbar-nav-left {
  text-transform: uppercase;
  flex: 1 1;
  margin-left: 20px;
}

.navbar-nav-right li:first-child a {
  display: flex;
  align-items: center;
}

.navbar-nav-right img {
  height: 20px;
  width: 20px;
  margin-right: 10px;
}

.navbar-nav-right li:nth-child(2) {
  margin: 0 5px;
}

/* akhir navbar */

/* Start  CSS For Button */

.btn {
  cursor: pointer;
  display: inline-block;
  background: none;
  border: 1px #000 solid;
  border-radius: 50px;
  padding: 7px 16px;
  line-height: 1.2;
  text-align: center;
  text-decoration: none;
}

.btn-dark-outline {
  border-color: #000;
  color: #000;
}

.btn-dark-outline:hover {
  background-color: azure;
  font-size: 20px;
}

.btn-dark {
  background-color: #000;
  color: white;
}

.btn-dark:hover {
  color: #f8f408;
}

.btn-light-outline {
  border-color: white;
  background-color: red;
  color: white;
}
.btn-light-outline:hover {
  background-color: #f8f408;
  color: black;
}

/* End CSS For Button */

/* Start CSS BOX  */
.box {
  margin-bottom: 30px;
}

.box-inner {
  max-width: 700px;
  margin: 0 auto;
}

.box-text {
  max-width: 500px;
  text-align: center;
  justify-self: center;
  padding: 20px 0;
}
.box-text.btn {
  margin-top: 20px;
}

.box-f .box-text {
  width: auto;
}

.text-md a {
  color: white;
}

.text-md a:hover {
  color: rgb(33, 188, 250);
}

/* End CSS BOX */

/* Start CSS footer */
.footer-container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 30px 30px;
}

.sosial {
  display: flex;
  align-items: center;
  margin: 20px 0;
}

.sosial img {
  width: 35px;
  height: 35px;
}
.sosial a {
  text-decoration: none;
  margin-right: 20px;
}

.divider {
  width: 100%;
  height: 1px;
  background-color: #006241;
  background-color: var(--color-primary);
}
/* End Css Footer */

/* Start Background style  */
.bg-primary {
  background-color: #006241;
  background-color: var(--color-primary);
  color: white;
}
.bg-secondary {
  background-color: #d50032;
  background-color: var(--color-secondary);
  color: white;
}
.bg-extra {
  background-color: #d4e9e2;
  background-color: var(--color-extra);
  color: #000;
}
.bg-dark {
  background-color: var(--color-dark);
  color: #000;
}

/* End Background style  */

/* Start Text style */

.text-center {
  text-align: center;
}

.text-xl {
  font-size: 50px;
  text-transform: uppercase;
  letter-spacing: 6px;
  font-weight: 600;
  margin-bottom: 20px;
}
.text-lg {
  font-size: 40px;
  text-transform: uppercase;
  margin-bottom: 20px;
}
.text-md {
  font-size: 24px;
  margin-bottom: 20px;
}
.text-sm {
  font-size: 19px;
  margin-bottom: 20px;
}

.img-wid {
  margin: 0;
  height: 60%;
  width: auto;
}

/* End Text style */

/* Start Padding style */
.py-lg {
  padding: 40px 0;
  width: 100%;
}
.py-md {
  padding: 20px 0;
}
.py-sm {
  padding: 10px 0;
}
/* End Padding style */

/* Start Hamburger style */
.hamburger {
  cursor: pointer;
  width: 24px;
  height: 24px;
  position: relative;
  background: none;
  border: none;
  z-index: 10;
  transition: all 0.25s;
  display: none;
}

.hamburger-top,
.hamburger-middle,
.hamburger-bottom {
  position: absolute;
  top: 0;
  left: 0;
  width: 24px;
  height: 2px;
  background: black;
  transform: rotate(0);
  transition: all 0.5s;
}

.hamburger-middle {
  transform: translateY(7px);
}
.hamburger-bottom {
  transform: translateY(14px);
}

.open .hamburger-top {
  transform: rotate(45deg) translateY(6px) translateX(6px);
}

.open .hamburger-middle {
  display: none;
}

.open .hamburger-bottom {
  transform: rotate(-45deg) translateY(6px) translateX(-6px);
}

/* End Hamburger style */

/* Start Mobile Menu Style  */
.mobile-menu {
  position: fixed;
  top: 85px;
  right: 0;
  color: black;
  background-color: white;
  width: 90%;
  height: 100%;
  padding: 30px;
  box-shadow: inset 0 4px 3px -3px rgb(0 0 0 / 10%), inset 0 4px 2px -2px rgb(0 0 0 / 7%);
  transition: all 0.3s;
}
.mobile-menu img {
  width: 20px;
  height: 20px;
  margin-right: 10px;
}

.mobile-menu ul {
  line-height: 3;
  border-bottom: #777 solid 1px;
  padding-bottom: 10px;
  margin-bottom: 30px;
}

.mobile-menu a {
  text-decoration: none;
  font-size: 20px;
}

.mobile-menu a:hover {
  color: #006241;
  color: var(--color-primary);
}
.mobile-menu div {
  margin-top: 20px;
}

.mobile-menu div a {
  display: flex;
  align-items: center;
  font-size: 16px;
}

.hidden {
  transform: translateX(100%);
}

.no-scroll {
  overflow: hidden;
}

/* End Mobile Menu Style  */

/* Start Css For Grid */
.grid-col-2 {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2rem;
  gap: 2rem;
  align-items: center;
  justify-content: space-between;
}

.grid-reversed:first-child {
  order: 2;
}
/* End Css For Grid */

@media (max-width: 768px) {
  .hamburger {
    display: block;
  }
  .grid-col-2 {
    grid-template-columns: 1fr;
  }
  .grid-reversed:first-child {
    order: 0;
  }
  .navbar-brand img {
    width: 40px;
    height: 40px;
  }
  .navbar .navbar-nav-left,
  .navbar .navbar-nav-right {
    display: none;
  }
  .footer {
    text-align: center;
  }
}

@media (max-width: 960px) {
  .text-xl {
    font-size: 30px;
  }

  .text-lg {
    font-size: 25px;
  }
  .text-md {
    font-size: 19px;
  }
}

@media (min-width: 1440px) {
  .box {
    max-width: 1440px;
    margin-right: auto;
    margin-left: auto;
  }
}

